#search-button {
  float: left;
  margin: 10px 0;
  margin-bottom: 25px;
  background-color: rgb(216,216,216);
  font-weight: 400;
  text-transform: uppercase;
  border-radius: 0px 8px;
}

.page-title, h1.header {
  font-family: 'Josefin Slab', serif;
  font-size: 2.5rem;
  padding: 5px;
  text-transform: uppercase;
}

h1.header{
  margin: 20px 0;
  text-align: center;
}

.page-wrapper {
  display: grid;
  grid-template-areas: 'nav' 'hero' 'search';
  grid-template-rows: repeat(3, auto);
}

.nav {
  grid-area: nav;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
  padding: 10px;
}

.hero-img {
  grid-area: hero;
}

.container-fluid {
  grid-area: search;
}

.search-form{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.date-input {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.start-input, .end-input {
  margin: 0 20px;
}

input[type="date"] {
  border: none;
  border-bottom: 1px solid black;
  border-radius: 5px;
  text-align: center;
  min-width: 225px;
}

.end-input {
  margin-bottom: 20px;
}

input.topic-input {
  border: none;
  border-bottom: 1px solid black;
  border-radius: 5px;
  min-width: 225px;
  padding-left: 10px;
}

body {
  font-family: 'Quicksand', sans-serif;
}

h3.no-results{
  text-align: center;
  font-weight: 400;
  margin-bottom: 100px;
}

.list-overflow-container{
  border: none;
}

.save-btn {
  float: right;
  color: #000;
  text-transform: uppercase;
  border-bottom: 1px solid black;
  padding: 5px 10px;
  border-radius: 8px;
  margin-bottom: 20px;
}

.save-btn:hover {
  border-bottom: 1px solid #417B5A;
  color: #417B5A;
}

.article-title{
  font-weight: 400;
  text-transform: uppercase;
}

#results-container{
  margin-left: auto;
  margin-right: auto;
}

.description{
  max-width: 800px;
}

.first-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}


